*,
::after,
::before {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}

a {
    color: inherit;
    text-decoration: none;
}

a:hover {
    color: inherit;
    text-decoration: none;
}

p {
    font-size: 16px;
}

h1 {
    font-size: 24px;
}

h2 {
    font-size: 22px;
    font-weight: normal;
    letter-spacing: 0.3px;
}

label {
    color: #343a40;
}

h3,
h4,
h5,
h6 {
    font-size: 20px;
    font-weight: normal;
}

h3.form-title {
    font-size: 22px;
}

.error-text {
    font-size: 14px;
    margin-top: 2px;
}

.cover {
    object-fit: cover;
}

.app {
    position: relative;
    width: 100%;
    height: auto;
    color: #ffffff;
}

.app__img-bg {
    width: 100%;
    height: 100vh;
}

.app__above-img {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.45);
}

.header__icon {
    width: 30px;
}

.user-anchor {
    border-left: 1px solid rgba(255, 255, 255, 0.5);
}

.header .social-title {
    font-size: 65px;
}

button.custom-btn-light {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    padding: 5px 30px;
    border-radius: 0px;
}

.modal-content {
    border-radius: 0px !important;
    background-color: whitesmoke !important;
}

.cursor-pointer {
    cursor: pointer !important;
}

.form-control {
    border-radius: 0px;
}

.btn {
    border-radius: 0px;
}

.user-modal {
    margin-top: 10px;
}

.user-modal .user-modal__name {
    font-size: 15px;
    padding: 13px;
    font-weight: 500;
    border-bottom: 1px solid rgba(64, 80, 108, .1);
    width: 200px;
    padding-left: 17px;
}

.user-modal .user-modal__subname {
    display: block;
    margin-top: 2px;
    font-size: 12px;
    color: #657389;
    font-weight: 400;
}

.user-modal .user-modal__favorite {
    padding: 13px;
    font-size: 13px;
    line-height: 1;
    cursor: pointer;
    border-bottom: 1px solid rgba(64, 80, 108, .1);
    padding-left: 17px;
}

.user-modal .user-modal__log-out {
    padding: 17px;
    font-size: 13px;
    /* color: #657389; */
    font-weight: bold;
    line-height: 1;
    cursor: pointer;
    padding-left: 17px;
}

.MuiPopover-paper {
    overflow-y: visible !important;
    overflow-x: visible !important;
}

.MuiPaper-root::after {
    content: "";
    position: absolute;
    right: 45%;
    top: -14px;
    border: 7px solid transparent;
    border-bottom-color: #fff;
}

.favorite__icon {
    font-size: 30px !important;
    color: rgb(190, 54, 54);
}

.MuiPaper-rounded {
    border-radius: 8px !important;
    border: 0px;
    box-shadow: 0 1px 2px 0 rgba(64, 80, 108, .2), 0 0 24px -1px rgba(64, 80, 108, .2) !important;
}

.user-anchor__img {
    width: 48px;
    height: 48px;
    object-fit: cover;
    border-radius: 20px;
}

.user-anchor__img::after {
    content: "";
    width: 20px;
    background: green;
}

.user-anchor__name {
    font-size: 17px;
    margin-left: 12px;
}

.search .search__field {
    background: rgba(255, 255, 255, 0.6);
    border: 0px;
    width: 200px;
    height: 38px;
    padding: 4px 5px;
    outline: none;
}

.main__social-text {
    font-weight: 300;
    font-size: 19px;
    line-height: 28px;
}

.popular-products {
    margin-bottom: 0;
    font-size: 28px;
    font-weight: 100;
}

.swiper-pagination-bullet-active {
    background: #3b3636 !important;
}

.swiper-container {
    width: auto;
    height: auto;
    min-height: 420px;
}

.product-card__img {
    position: relative;
}

.product-card__img .img {
    max-width: 100%;
    height: 235px;
    max-height: 300px;
    object-fit: cover;
}

.product-card__img .favorite-icon {
    position: absolute;
    top: 0px;
    right: 15px;
    font-size: 25px;
}

.product-card__img .favorite-icon__active {
    color: rgb(190, 54, 54);
}

.product-card__img .favorite-icon__disabled {
    color: rgba(0, 0, 0, 0.26);
}

.product-card__title .title {
    font-size: 18px;
    text-overflow: ellipsis;
    word-wrap: none;
    white-space: nowrap;
    overflow: hidden;
}

@media (max-width: 992px) {
    .add-product {
        display: none;
    }
}

@media (max-width: 768px) {
    .user-anchor {
        border-left: none;
    }

    .header .social-title {
        font-size: 57px;
    }
}

@media (max-width: 576px) {
    .favorite {
        display: none;
    }

    .user-anchor__name {
        display: none;
    }

    .header .social-title {
        font-size: 48px;
    }

    .MuiPaper-root::after {
        right: 22%;
    }
}